@font-face {
  font-family: 'OptimaLTStd-Bold';
  src: url("fonts/OptimaLTStd-Bold.eot?#iefix") format("embedded-opentype"), url("fonts/OptimaLTStd-Bold.otf") format("opentype"), url("fonts/OptimaLTStd-Bold.woff") format("woff"), url("fonts/OptimaLTStd-Bold.ttf") format("truetype"), url("fonts/ptimaLTStd-Bold.svg#OptimaLTStd-Bold") format("svg");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'OptimaLTStd-DemiBold';
  src: url("fonts/OptimaLTStd-DemiBold.eot?#iefix") format("embedded-opentype"), url("fonts/OptimaLTStd-DemiBold.otf") format("opentype"), url("fonts/OptimaLTStd-DemiBold.woff") format("woff"), url("fonts/OptimaLTStd-DemiBold.ttf") format("truetype"), url("fonts/OptimaLTStd-DemiBold.svg#OptimaLTStd-DemiBold") format("svg");
  font-weight: 600;
  font-style: normal;
}
body {
  background-color: #f5f5f5;
  font-family: 'OptimaLTStd-DemiBold', sans-serif;
  color: #072A2B;
}

h6 {
  font-size: 1.2rem;
  text-transform: uppercase;
}

p {
  font-size: 1rem;
}

p.text-big {
  font-size: 1.8rem;
}

a {
  color: white;
}
a:hover, a:active, a:focus {
  color: white;
  text-decoration: underline;
}

.container {
  background-color: white;
  padding: 0px;
}
@media (min-width: 768px) {
  .container {
    max-width: 600px;
  }
}

.confirmation {
  background-image: url("images/fondo.png");
  padding: 50px 0px;
  text-align: center;
}
.confirmation a:not(.btn) {
  color: #072A2B;
}
.confirmation a:not(.btn):hover, .confirmation a:not(.btn):active, .confirmation a:not(.btn):focus {
  color: #072A2B;
  text-decoration: underline;
}

.code {
  background-color: #1AC6D3;
  color: white;
  padding: 50px 0px;
  text-align: center;
}

.place {
  background-color: #1AC6D3;
  color: white;
  padding: 50px 0px;
  text-align: center;
}

.btn-primary {
  background-color: #1AC6D3;
  border-color: #1AC6D3;
  color: white;
}
.btn-primary:hover, .btn-primary:active, .btn-primary:focus {
  background-color: #16AFB2;
  border-color: #16AFB2;
  color: white;
}

.adorno {
  max-width: 120px;
  display: block;
  margin: 10px auto;
}

